.App {
  text-align: center;
}
 .heading{
  margin-bottom: 30px;
  color: rgb(235, 169, 82);
  background-color: aquamarine;
}
*{
  margin: 0;
  padding: 0;
}
.cardContainer{
  /* background-color: lightblue; */
  width: 80vw;
  margin: auto;
  display:grid;
  grid-template-columns: 1fr 1fr 1fr  ;
  grid-gap:30px;

}
.cardContent{
  background:linear-gradient(to left,rgb(71, 90, 214) 0%,rgba(234, 61, 234, 0.828)100%);
 display:flex;
 margin-top: 30px;
 flex-direction: column;
 padding: 30px;
 height: 50vh;
 width: 20vw;
 color: aliceblue;
 border: 1px solid grey;
 border-radius: 7px;
 transition: transform .2s ease-in-out;
}
.cardContent:hover{
  transform: scale(1.1);
}
.name{
  font-size:1.5rem;
  margin-bottom: 50px;
}
.name:nth-child(1)
{
  color: rgba(242, 246, 250, 0.877);
}
.name>:last-child{
  margin-top: 30px;
  color: #15ed3c;
  font-size: 1.3rem;
  overflow:auto
}

.SearchBar{
  padding: 15px;
  border-radius: 20px;
  font-size:15px;
  border: 1px solid black;
}
.photo>img{
  border-radius: 7px;
}

